<template>
  <b-list-group-item style='border: none'>
    <h6>
      Skill: {{data.skillName}} Skill Proficiency: <span>
        <b-progress :value="data.skillRating" max="5"></b-progress>
      </span>
    </h6>
    <div class='d-flex w-100, justify-content-between'>
      <small class='text-muted'>
        <b-link @click="revealEditModal(data.skillID)">Edit</b-link>
      </small>
      <small class='text-muted'>
        <b-link @click="revealRemoveModal(data.skillID)">Remove</b-link>
      </small>
      <b-modal :id="'modal-skill-edit-' + data.skillID" hide-footer>
        <b-form>
          <SkillForm :data="data"></SkillForm>
        </b-form>
      </b-modal>
      <b-modal :id="'modal-skill-remove-' + data.skillID" hide-footer>
        <b-form @submit="$emit('delete-row')">
          <b-button type="submit" variant="danger">Are you sure?</b-button>
        </b-form>
      </b-modal>
    </div>
  </b-list-group-item>
</template>

<script>
/* eslint-disable */
import Vue from 'vue';
import { ProgressPlugin, FormPlugin, ModalPlugin, ListGroupPlugin, ButtonPlugin, LinkPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

const SkillForm = () => import('../form/SkillForm');

Vue.use(ListGroupPlugin);
Vue.use(FormPlugin);
Vue.use(ModalPlugin);
Vue.use(ButtonPlugin);
Vue.use(LinkPlugin);
Vue.use(ProgressPlugin);

export default {
  name: 'Skill',
  props:['data'],
  components: {
    SkillForm
  },
  methods:{
    async revealEditModal(_id){
      this.$bvModal.show('modal-skill-edit-' + _id);
    },
    async revealRemoveModal(_id){
      this.$bvModal.show('modal-skill-remove-' + _id);
    }
  },
}
</script>



